.app-list {
    margin-top: 30px;
    background-color: #3d5a80;
    border-radius: 4px;
    box-shadow: 15px 15px 30px rgba(0,0,0, .15);
    color: #fff;
}

@media screen and (max-width: 600px) {
    .app-list{
        display: flex;
        position: relative;
        flex-direction: column;
    }
  }